
    import {
        defineComponent,
        reactive,
        onMounted,
    } from "vue";
    import MaterialSelect from '@/components/material-select/index.vue'
    import FooterBtns from '@/components/footer-btns/index.vue'
    import {
        apiGetWebsite,
        apiSetWebsite
    } from "@/api/setting"
    export default defineComponent({
        components: {
            MaterialSelect,
            FooterBtns
        },
        setup() {
            // 表单数据
            let formData = reactive({
                name: '', // 网站名称
                web_favicon	: '', // 网站图标
                web_logo: '', // 网站logo
                login_image: '', // 登录页广告图
            })

            // 表单验证
            const rules = {
                name: [
                    {
                        required: true,
                        message: '请输入网站名称',
                        trigger: ['blur'],
                    },
                ],
            }

            // 获取备案信息
            const getWebsite = () => {
                apiGetWebsite()
                    .then((res: any) => {
                        console.log('res', res)
                        formData.name = res.name
                        formData.web_favicon = res.web_favicon
                        formData.web_logo = res.web_logo
                        formData.login_image = res.login_image
                    })
            }

            // 设置备案信息
            const setWebsite = () => {
                apiSetWebsite({
                        name: formData.name,
                        web_favicon: formData.web_favicon,
                        web_logo: formData.web_logo,
                        login_image: formData.login_image,
                    })
                    .then((res: any) => {
                        console.log('res', res)
                        getWebsite()
                    })
                    .catch((err: any) => {
                        console.log('err', err)
                    })
            }

            onMounted(() => {
                getWebsite()
            })

            return {
                formData,
                rules,
                getWebsite,
                setWebsite,
            }
        },
    })
